.about__section {
  margin: 0 auto;
  background-color: var(--main-bckgr-color);
  @media screen and(min-width: 320px) and(max-width: 767px) {
    padding: 125px 0px 39px 0px;
    background-image: 
    // linear-gradient(
    //     rgba(47, 48, 58, 0.4), 
    //     rgba(47, 48, 58, 0.4)
    //     ), 
        url(./../images/about/about-bg-mobile.png);
    background-repeat: no-repeat;
    background-position: right 0px bottom 100px;
    background-size: contain;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: 
    // linear-gradient(
    //     rgba(47, 48, 58, 0.4), 
    //     rgba(47, 48, 58, 0.4)
    //     ), 
        url(./../images/about/about-bg-mobile-2x.png);
    }
  }

  @media screen and(min-width: 768px) {
    padding: 130px 0px 66px 0px;
  }

  @media screen and (min-width: 1200px) {
    // max-width: 1280px;
    padding: 125px 0px 39px 0px;
    background-image: 
    // linear-gradient(
    //     rgba(47, 48, 58, 0.4), 
    //     rgba(47, 48, 58, 0.4)
    //     ), 
        url(./../images/about/about-bg.png);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: 
    // linear-gradient(
    //     rgba(47, 48, 58, 0.4), 
    //     rgba(47, 48, 58, 0.4)
    //     ), 
        url(./../images/about/about-bg-2x.png);
    }
  }
}

.about__presentation {
  // background-color: var(--main-bckgr-color);
  display: block;
  margin: 0 auto;

  @media screen and(min-width: 320px) {  
    padding-right: 41px;
    padding-left: 39px;
    margin-bottom: 26px;
  }

  @media screen and(min-width: 768px) {
    text-align: center;
    padding-right: 115px;
    padding-left: 123px;
    margin-bottom: 50px;
  }

  @media screen and (min-width: 1200px) {
    text-align: center;
    padding-right: 237px;
    padding-left: 240px;
    margin-bottom: 34px;
  }
}

.about__article {
  // background-color: var(--main-bckgr-color);

  @media screen and (min-width: 768px) {
    @include flex ($df:flex, $jcc:center, $aic:center);
    flex-wrap: nowrap;
  }
}

.about__image {
  display: block;
  margin: auto;
  // width: 280px;

  @media screen and (min-width: 320px) {
    margin-bottom: 46px;
  }

  @media screen and (min-width: 768px) {
    // width: 340px;
    margin-bottom: 0;
  }

  // @media screen and (min-width: 1200px) {
  //   width: 516px;
  // }
}

.about__paragraph {
  padding: 0 30px;
  margin-bottom: 23px;
  width: 100%;

  @media screen and (min-width: 768px) {
    padding: 50px 0 0 0;
    margin-left: 17px;
    width: 340px;
  }

  @media screen and (min-width: 1200px) {
    padding: 72px 0 0 0;
    margin-left: 74px;
    width: 463px;
  }
}

.about__text {
  font-family: DM Sans;
  font-style: normal;
  @include font($fs: 12px, $fw: 500, $lh: 1.54, $ls: 0);
  color: var(--third-text-color);
  margin-bottom: 34px;

  @media screen and (min-width: 768px) {
    @include font($fs: 14px, $fw: 500, $lh: 1.79, $ls: 0);
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1200px) {
    @include font($fs: 16px, $fw: 500, $lh: 1.76, $ls: 0.04em);
    margin-bottom: 35px;
  }
}

.about__description {
  font-family: DM Sans;
  font-style: normal;
  @include font($fs: 12px, $fw: 500, $lh: 1.66, $ls: 0);
  color: var(--fourth-text-color);
  margin-bottom: 17px;

  @media screen and (min-width: 768px) {
    @include font($fs: 14px, $fw: 500, $lh: 1.79, $ls: 0.04em);
    margin-bottom: 62px;
  }

  @media screen and (min-width: 1200px) {
    @include font($fs: 14px, $fw: 500, $lh: 1.93, $ls: 0.04em);
    margin-bottom: 45px;
  }
}

.about__button-open {
  margin-right: auto;
  width: 176px;
  height: 44px;
  border: none;
  padding: 0;
  font-family: DM Sans;
  font-style: normal;
  @include font($fs: 14px, $fw: 700, $lh: 1.29, $ls: 0);
  @include flex ($df:flex, $jcc:center, $aic:center);
  color: var(--main-btn-color);
  background: var(--second-btn-color);
  border-radius: 22px;
  cursor: pointer;
}

.about__button-icon {
    fill: currentColor;
    margin-left: 13.62px;
}